    box-shadow: 0 0 6px rgba(0, 0, 0, 0.16), 0 6px 12px rgba(0, 0, 0, 0.32);
}
.chat-launcher:before,
.chat-launcher:after {
    font-family: "Material-Design-Iconic-Font";
    font-size: 20px;
    position: absolute;
    right: 14px;
    top: 6px;
    transition: opacity 130ms linear, -webkit-transform 180ms linear;
    transition: transform 180ms linear, opacity 130ms linear;
    transition: transform 180ms linear, opacity 130ms linear, -webkit-transform 180ms linear;
}
.chat-launcher:before {
    content: "\f266";
    opacity: 1;
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
}
.chat-launcher:after {
    content: "\f136";
    opacity: 0;
    -webkit-transform: rotate(-30deg);
    transform: rotate(-30deg);
}
.chat-launcher:hover {
    background: #f8d021;
}
.chat-launcher.active:before {
    opacity: 0;
    -webkit-transform: rotate(70deg) scale(0);
    transform: rotate(70deg) scale(0);
}
.chat-launcher.active:after {
    opacity: 1;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
