.rdio-bnt {
  display: inline-block;
  position: relative;
  padding-left: 24px;
  margin-bottom: 12px;
  cursor: pointer;
  font-family: Aspira;
    font-size: 14px;
    font-weight: 600;
    color: #000; margin-left:20px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.rdio-bnt input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.rdio-bnt .checkmark {
  position: absolute;
  top: 2px;
  left: 0;
  height:15px;
  width:15px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.rdio-bnt:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.rdio-bnt input:checked ~ .checkmark {
  border:1px solid #bdbdbd;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.rdio-bnt .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.rdio-bnt input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.rdio-bnt .checkmark:after {
 	top: 1px;
	left: 1px;
	width: 11px;
	height: 11px;
	border-radius: 50%;
	background:#05a7c4;
}

